:root {
    --primary: #000759  ;
    --secondary: #0067c2;
    --dark: #131A26;
    --light: #eeeeee;
    --grey: #888888;
  
    --font: 'tahoma', sans-serif;
    --font-quote: 'Fira Code', monospace;
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* If you set box-sizing: border-box; on an element, padding and border are included in the width and height: */
    font-family: var(--font);
  }
  
  body {
    background-color: var(--grey);
  }
  
  .App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
  }
  
  .Quote {
    position: relative;
    /* An element with position: relative; is positioned relative to its normal position. */
  }
  
  .author {
    display: inline-block;
    /* One common use for display: inline-block is to display list items horizontally instead of vertically. The following example creates horizontal navigation links: */
    padding: 0.75rem 1.5rem;
    color: var(--light);
    border-radius: 0.5rem;
    border: 2px solid var(--light);
    font-size: 0.8rem;
    letter-spacing: 1px;
    max-width: 30vw;
    overflow: hidden;
    text-overflow: ellipsis;
    /* adds the standard three dots to hide any overflow like overfl... */
    white-space: nowrap;
    /* makes sure nothing wraps down to a next line */
    background-color: var(--primary);
    font-weight: 700;
    transform: translate(70px, -50%);
    /* We set position absolute and then right 0 and bottom 0 to stick this item to the bottom right hand corner */
    right: 0px;
    bottom: 0px;
  }
  
  blockquote {
    max-width: 50vw;
    font-size: 1.2rem;
    background-color: var(--dark);
    padding: 3rem;
    border-radius: 1rem;
    border: 2px solid var(--light);
    color: white;
    position: relative;
  }
  
  button {
    appearance: none;
    border: none;
    outline: none;
    /* Reset everything */
    background-color: var(--primary);
    color: white;
    padding: 0.75rem 1.5rem;
    border-radius: 0.5rem;
    margin-top: 64px;
    margin-bottom: 64px;
    transition: 0.4s;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -100%);
    border: 2px solid var(--light);
  
  }
  
  #quote-box {
    position: relative;
  }
  
  button:hover {
    background-color: var(--secondary);
    transform: translate(-50%, -100%) scale(110%);
  }

  .one, .two, .three {
    font-family: tahoma;
  }

  .two {
    font-size: 0.9rem;
  }

  .three {
    font-size: 0.8rem;
  }

  @media only screen and (max-width: 900px) {
    blockquote {
      max-width: 65vw;
    }
    button {
      max-width: 40vw;
    }
  }

  @media only screen and (max-width: 700px) {
    blockquote {
      max-width: 85vw;
    }
    button {
      max-width: 50vw;
    }
  }

